import React, { useEffect } from 'react';
import { useEditor, EditorContent } from '@tiptap/react';
import StarterKit from '@tiptap/starter-kit';
import Highlight from '@tiptap/extension-highlight';
import Image from '@tiptap/extension-image';
import CodeBlockLowlight from '@tiptap/extension-code-block-lowlight';
import { createLowlight } from 'lowlight';
import javascript from 'highlight.js/lib/languages/javascript';
import 'highlight.js/styles/monokai.css'; // 하이라이팅 스타일

// lowlight 인스턴스 생성
const lowlight = createLowlight();
lowlight.register('javascript', javascript);

// Toolbar 컴포넌트
const Toolbar = ({ editor }) => {
  if (!editor) {
    return null;
  }

  return (
    <div style={{ marginBottom: '10px', display: 'flex', gap: '10px' }}>
      <button
        onClick={() => editor.chain().focus().toggleBold().run()}
        style={{
          padding: '5px 10px',
          border: '1px solid #ddd',
          borderRadius: '4px',
          backgroundColor: editor.isActive('bold') ? '#007bff' : '#fff',
          color: editor.isActive('bold') ? '#fff' : '#000',
        }}
      >
        Bold
      </button>
      <button
        onClick={() => editor.chain().focus().toggleItalic().run()}
        style={{
          padding: '5px 10px',
          border: '1px solid #ddd',
          borderRadius: '4px',
          backgroundColor: editor.isActive('italic') ? '#007bff' : '#fff',
          color: editor.isActive('italic') ? '#fff' : '#000',
        }}
      >
        Italic
      </button>
      <button
        onClick={() => editor.chain().focus().toggleHighlight().run()}
        style={{
          padding: '5px 10px',
          border: '1px solid #ddd',
          borderRadius: '4px',
          backgroundColor: editor.isActive('highlight') ? '#007bff' : '#fff',
          color: editor.isActive('highlight') ? '#fff' : '#000',
        }}
      >
        Highlight
      </button>
      <button
        onClick={() => editor.chain().focus().toggleCodeBlock().run()}
        style={{
          padding: '5px 10px',
          border: '1px solid #ddd',
          borderRadius: '4px',
          backgroundColor: editor.isActive('codeBlock') ? '#007bff' : '#fff',
          color: editor.isActive('codeBlock') ? '#fff' : '#000',
        }}
      >
        Code Block
      </button>
      <button
        onClick={() => editor.chain().focus().setImage({ src: prompt('Enter image URL') }).run()}
        style={{
          padding: '5px 10px',
          border: '1px solid #ddd',
          borderRadius: '4px',
        }}
      >
        Insert Image
      </button>
    </div>
  );
};

const MyEditor = ({ content, setContent }) => {
  const editor = useEditor({
    extensions: [
      StarterKit.configure({
        codeBlock: false, // StarterKit 기본 codeBlock 비활성화
      }),
      Highlight,
      Image.configure({ inline: true, allowBase64: true }),
      CodeBlockLowlight.configure({
        lowlight,
      }),
    ],
    content: content,
    onUpdate: ({ editor }) => {
      setContent(editor.getHTML());
    },
  });

  useEffect(() => {
    if (editor && content) {
      editor.commands.setContent(content);
    }
  }, [editor, content]);

  if (!editor) {
    return <div>에디터를 불러오는 중입니다...</div>;
  }

  return (
    <div>
      <Toolbar editor={editor} />
      <div style={{ border: '1px solid #ddd', borderRadius: '8px', padding: '10px', background: '#fff' }}>
        <EditorContent editor={editor} />
      </div>
    </div>
  );
};

export default MyEditor;
