import React, { createContext, useState, useEffect } from "react";
import api from "../api/Axios";
import { logout } from "../api/AuthApi";

export const AuthContext = createContext();

export const AuthProvider = ({ children }) => {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [userInfo, setUserInfo] = useState(null);
  const [accessToken, setAccessToken] = useState(localStorage.getItem("accessToken") || null);

  const updateAccessToken = (newAccessToken) => {
    //console.log("Updating AccessToken in Context:", newAccessToken);
    localStorage.setItem("accessToken", newAccessToken);
    setAccessToken(newAccessToken);
  };
  useEffect(() => {
    const accessToken = localStorage.getItem("accessToken");
    const storedUser = localStorage.getItem("userInfo");


    if (accessToken) {
      // 서버에서 토큰 검증 요청
      api
        .get("/auth/validate-token", { headers: { Authorization: `Bearer ${accessToken}` } })
        .then(() => {
          setUserInfo(JSON.parse(storedUser));
          setIsLoggedIn(true);
        })
        .catch(() => {
          handleLogout(); // 토큰이 유효하지 않으면 로그아웃 처리
        });
    } else {
      handleLogout(); // 토큰이 없으면 로그아웃 처리
    }
  }, []);

  const handleLogin = (user, accessToken) => {
    localStorage.setItem("accessToken", accessToken);
    localStorage.setItem("userInfo", JSON.stringify(user));
    setUserInfo(user);
    setIsLoggedIn(true);
  };

  const handleLogout = async () => {
    try {
      const userInfo = JSON.parse(localStorage.getItem("userInfo"));
      if (userInfo?.username) {
        await logout(userInfo.username);
      }
    } catch (error) {
      console.error("Logout API call failed:", error);
    } finally {
      localStorage.removeItem("accessToken");
      localStorage.removeItem("userInfo");
      setUserInfo(null);
      setIsLoggedIn(false);
    }
  };

  return (
    <AuthContext.Provider value={{ isLoggedIn, userInfo, handleLogin, handleLogout, updateAccessToken }}>
      {children}
    </AuthContext.Provider>
  );
};
